@import '/src/styles/styles.scss';

.congrats {
  @include main-font(500, 24px, 33px, normal);
  text-align: center;
  margin-bottom: 24px;
}
.text {
  @include main-font(500, 16px, 22px, normal);
  text-align: center;
}
.button_profile {
}
.header__container {
  width: 100%;
  min-height: 60px;
  padding: 15px 0;
  background-color: #ffffff;

  @media screen and (max-width: 767px) {
    position: fixed;
    z-index: 10000000;
  }

  @media screen and (min-width: 768px) {
    min-height: 80px;
    padding: 20px 0;
  }

  @media screen and (min-width: 1280px) {
    padding-right: 0;
    padding-left: 0;
  }
}

.containerInner {
  height: 30px;
  display: flex;
  justify-content: space-between;
  @media screen and (min-width: 768px) {
    height: 40px;
  }
}

.logoBox {
  display: flex;
  gap: 20px;
  min-width: 120px;
  text-decoration: none;

  @media screen and (min-width: 768px) {
    min-width: 180px;
  }
}

.img__wallet {
  @media screen and (min-width: 768px) {
    width: 40px;
    height: 40px;
  }
}

.text__wallet {
  display: flex;
  align-items: center;
  font-style: normal;
  font-family: PoppinsBold;
  font-size: 22px;
  line-height: calc(33 / 22);
  color: getColor('text-color');

  @media screen and (min-width: 768px) {
    font-size: 30px;
    line-height: calc(45 / 30);
  }
}
// ________________________________________
.exitBox {
  display: flex;
  min-width: 106px;
  align-items: center;
  justify-content: flex-end;

  @media screen and (min-width: 768px) {
    min-width: 160px;
  }
}
.name {
  display: flex;
  font-family: CirceRegular;
  font-style: normal;
  font-weight: 400;
  font-size: 18px;
  line-height: calc(27 / 18);
  text-align: right;
  color: getColor('secondary-text-color');
}
